#wpadminbar #wp-admin-bar-jeg-kit-pro {
	background-color: #faa71c;

	&:hover {
		background-color: #e5941f;
	}

	.ab-item {
		background: unset;
		color: white;
	}
}

#toplevel_page_jkit {
	a:hover {
		.wp-menu-image {
			img {
				filter: brightness(0) saturate(100%) invert(74%) sepia(13%) saturate(1685%) hue-rotate(182deg) brightness(98%) contrast(93%);
			}
		}

	}

	.wp-menu-image {
		img {
			opacity: 1;
			padding: 7px 0 0;
		}
	}

	&.wp-has-current-submenu {
		&.wp-menu-open {
			.wp-menu-image {
				img {
					filter: brightness(0) saturate(100%) invert(100%);
				}
			}

		}
	}

	.wp-submenu {
		li:last-child {
			a[href*="jegkit.com"] {
				margin: 0px 5px;
				padding: 8px 14px;
				display: flex;
				gap: 5px;
				justify-content: center;
				align-items: center;
				background: linear-gradient(180deg, #ffb12d 0, #f59e0b 100%);
				color: white;
				border-radius: 5px;
				transition: all 0.3s ease;

				&:hover,
				&:focus {
					box-shadow: unset;
					transform: scale(0.94);
					background: linear-gradient(180deg, #F1A627 0%, #E89812 100%);
				}
			}
		}
	}
}